import vueTemplateCompiler from 'vue-template-compiler';
<template>
<div class="">
     <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主题部分 -->
    <el-card class="box-card">
       <!-- 栅栏布局 -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
          v-model="query"
            placeholder="请输入内容"
            class="input-with-select"
          >
            <el-button
            @click="getList()"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button
            type="primary"
            @click="add"
            >添加商品</el-button
          >
        </el-col>
      </el-row>
      <!-- 表格部分 -->
      <el-table border :data="list" stripe style="width: 100%">
        <el-table-column type="index" label="#" width="50"> </el-table-column>
        <el-table-column prop="goods_name" label="商品名称" width="700">
        </el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)" width="110">
        </el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" width="80">
        </el-table-column>
        <el-table-column prop="upd_time" label="创建时间" width="180">
          <template slot-scope="scope">
           {{scope.row.upd_time|timefn}}
          </template>
        </el-table-column>
        <!-- 操作按钮 -->
        <el-table-column label="操作" width="120">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
            ></el-button>
            <!-- 删除 -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="del(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          height="50"
          :current-page="pagenum"
          :page-sizes="[5, 20, 30, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
        </div>
    </el-card>
</div>
</template>

<script>
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
     list:[],
    //  添加弹框所需
     dialogFormVisible: false,
     formLabelWidth: "100px",
      // 添加提示输入框
      ruleForm: {
        name: "",
      },
       // 规则有这个才有小红点
      rules: {
        name: [
          { required: true, message: "分类名称不能为空", trigger: "blur" },
        ]
      },
      // 分页渲染所需
      query:'',
      pagenum:1,
      pagesize:10,
      total:0,
      // 设置分页所需

   }
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    // 获取列表
    async getList(num=this.pagenum,size=this.pagesize){
      let {data} = await this.$http.get(`goods?query=${this.query}&pagenum=${num}&pagesize=${size}`)
      console.log(data.data)
      this.list=data.data.goods
      this.total=data.data.total
      this.pagesize = size;
      this.pagenum = num;
      console.log(this.total,this.list)
    },
      // 添加商品
      add(){
        this.dialogFormVisible = true
      },
      // 分页部分
    // num必须要有默认值不然就报错
    handleSizeChange(size, num = this.pagenum) {
      this.getList(num, size);
    },
    // 这里是size必须有默认值，不然不行
    handleCurrentChange(num, size = this.pagesize) {
      this.getList(num, size);
    },
  // 删除商品
   // 删除
    del(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          console.log(id)
          this.$http.delete("goods/" + id).then((res) => {
            console.log(res);
            this.getList()
          });
          
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted(){
    this.getList()
  }
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.el-card{
  margin-top: 15px;
  font-size: 12px;
}
.el-.el-table td div {
  font-size: 12px;
}
.el-table__body tr .el-button, .el-table__body td .el-button{
  width: 40px;
}
</style>
