<template>
  <div class="store">
    <el-container>
      <!-- 头部 -->
      <el-header>
        <div class="left">
          <img src="../assets/logo.png" alt="" />
          <p>电商后台管理系统</p>
        </div>
        <el-button type="info" @click="out">退出</el-button>
      </el-header>
      <el-container>
        <!-- 侧边栏 -->
        <el-aside width="">
          <p @click="isCollapse=!isCollapse">|||</p>
          <el-menu
          unique-opened
          
            router
            text-color="#fff"
            background-color=" #333744"
            :default-active="xb"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            :collapse="isCollapse"
          >
            <el-submenu :index="item.id+''" :key="index" v-for="(item,index) in list">
              <template slot="title">
                <i :class="icon[index]"></i>
                <span slot="title">{{item.authName}}</span>
              </template>
              <el-menu-item-group :key="i" v-for="(child,i) in item.children">
                <el-menu-item :index="'/store/'+child.path" @click="jump('/store/'+child.path)"><i class="el-icon-menu"></i>{{child.authName}}</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!-- 主体部分 -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      isCollapse: false,
      // 侧边栏图标要用
      icon:["el-icon-user-solid","el-icon-s-help","el-icon-s-goods","el-icon-s-claim","el-icon-s-marketing"],
      // 侧边栏数据
      list:[],
      // 搞一个下标用来搞侧边栏高亮
      xb:sessionStorage.getItem("index")||''
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {
    // 侧边栏监听
    $route(to,from){
      if(to.path=='/store/sy'){
        this.xb=''
      }else{
        this.xb = to.fullPath+''
      }
      sessionStorage.setItem("index",to.fullPath+'')
    }
  },
  // 组件方法
  methods: {
    // 退出事件
    out() {

      this.$confirm('你确定退出嘛?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          sessionStorage.removeItem("token");
      this.$router.push("/login");
          this.$message({
            type: 'success',
            message: '退出成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });          
        });
      
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    // 获取侧边栏数据
    async getList(){
      let {data} = await this.$http.get("menus")
      this.list = data.data
      console.log(this.list)
    },
    // 测边跳转的事件
    jump(i){
      console.log(i)
      sessionStorage.setItem("index",i+'')
      this.xb=i
    }
  },
  mounted(){
    this.getList()
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss" scoped>
.store,
.el-container {
  height: 100%;
}
// 大体样式
.el-header {
  display: flex;
  align-items: center;
  background-color: #373d41;
  justify-content: space-between;
  line-height: 60px;
  padding: 0 20px;
  box-sizing: border-box;
  flex-shrink: 0;
}

.el-aside {
  background-color: #333744;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
}

// 头部
.left {
  display: flex;
  align-items: center;
  img {
    margin-right: 15px;
    width: 50px;
  }
  p {
    font-size: 22px;
    color: #fff;
  }
}

// 侧边栏
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
p{
  background-color: #4a5064;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 22px;
    cursor: pointer;
    letter-spacing: .12em;
}
</style>
