<template>
<div class="login">
    <div class="card">
        <div class="img">
            <img src="../assets/logo.png" alt="">
        </div>
        <!-- 输入部分 -->
        <el-form status-icon label-width="100px" class="demo-ruleForm">
        <div class="demo-input-suffix">
            <el-input
           
            prefix-icon="el-icon-user-solid"
            v-model="username">
            </el-input>
        </div>
  <div class="demo-input-suffix">
            <el-input
           type="password"
            prefix-icon="el-icon-lock"
            v-model="password">
            </el-input>
        </div>
 <el-row>
  <el-button type="primary" @click="getToken">登录</el-button>
  <el-button type="info" @click="reset">重置</el-button>
</el-row>
</el-form>
    </div>
</div>
</template>

<script>
export default {
  // 组件名称
  name: 'demo',
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data () {
   return {
       username:"admin",//用户名
       password:"123456"//密码
   }
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    //   登录
    async getToken(){
        let {data} = await this.$http.post("login",{
            username:this.username,
            password:this.password
        })
        // console.log(data)
        if(data.meta.status==200){
           sessionStorage.setItem("token",''+data.data.token)
        this.$router.push('/store')
        // 登录成功提示
        this.$message({
          message: '登陆成功',
          type: 'success'
        });
        }else{
           this.$message.error('登录失败！');
        this.reset()
        }
        
    },
    // 重置
    reset(){
         this.username="admin",//用户名
       this.password="123456"//密码
    }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style  lang="scss" scoped>
.login{
    background-color: #2b4b6b;
    height: 100%;
}
// 卡片部分
.card{
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
width: 450px;
    height: 304px;
    border-radius: 3px;
    top: 50%;
    // 图片部分
    .img{
            width: 130px;
    height: 130px;
    padding: 10px;
    border: 1px solid #efefef;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
    img{
        width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #efefef;
    }
    }
}

// 表单部分
.el-form{
    position: absolute;
    bottom: 20px;
    left: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    .el-input{
        margin-bottom: 20px;
    }
    .el-row{
        text-align: right;
        margin-top: 10px;
    }
}
</style>
