<template>
  <div class="">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主题部分 -->
    <el-card class="box-card">
      <!-- 按钮 添加角色 -->
      <el-row>
        <el-button type="primary" @click="add">添加分类</el-button>
      </el-row>
      <!-- 再来一个表格 -->
      <el-table
        :data="list"
        style="width: 100%;margin-bottom: 20px;"
        row-key="cat_id"
        border
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="cat_name" label="分类名称" width="180">
        </el-table-column>
        <el-table-column prop="cat_deleted" label="是否有效" width="">
          <template slot-scope="scope">
            <i class="el-icon-success" style="color:#67c23a;"></i>
          </template>
        </el-table-column>
        <el-table-column width="" label="排序">
          <template slot-scope="scope">
            <div
              style="background:#409eff;"
              class="hmw"
              v-if="scope.row.cat_level == 0"
            >
              一级
            </div>
            <div
              style="background:#67c23a;"
              class="hmw"
              v-if="scope.row.cat_level == 1"
            >
              二级
            </div>
            <div
              style="background:#e6a23c;"
              class="hmw"
              v-if="scope.row.cat_level == 2"
            >
              三级
            </div>
          </template>
        </el-table-column>
        <!-- 操作按钮 -->
        <el-table-column label="操作" prop="cat_id" width="180">
          <template slot-scope="scope">
            <!-- 编辑 -->
            <el-button type="primary" icon="el-icon-edit" @click="update(scope.row)">编辑</el-button>
            <!-- 删除 -->
            <el-button type="danger" icon="el-icon-delete" @click="del(scope.row.cat_id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          height="50"
          :current-page="pagenum"
          :page-sizes="[5, 4, 6, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!-- 添加的弹框 -->
    <el-dialog title="添加分类" :visible.sync="dialogFormVisible">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="分类名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>

        <el-form-item label="父级分类">
          <div class="block">
            <el-cascader
            v-model="ruleForm.value"
            collapse-tags
              :options="list2"
              :props="{ checkStrictly: true,value:'cat_id',label:'cat_name'}"
              clearable
            ></el-cascader>
          </div>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <!-- 这里的参数必须要是和ref一样的!!! -->
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
      </div>
    </el-dialog>
    <!-- 修改的弹框 -->
    <el-dialog title="修改分类" :visible.sync="dialogFormVisibleUpdate">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="分类名称" prop="name">
          <el-input v-model="ruleFormUpdate.name"></el-input>
        </el-form-item>
       
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleUpdate = false">取 消</el-button>
        <el-button type="primary" @click="updated('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      list: [],
      
      pagesize: 5,
      pagenum: 1,
      type: "", //表示一层二层的分类列表
      total: 0,
      //  添加弹框所需
      dialogFormVisible: false,
      // 修改弹框
      dialogFormVisibleUpdate: false,
      formLabelWidth: "100px",
      // 添加提示输入框
      ruleForm: {
        value:[],
        name: ""
      },
      // 规则有这个才有小红点
      rules: {
        name: [
          { required: true, message: "分类名称不能为空", trigger: "blur" },
        ],
      },
      // 添加分类的列表
      addList: [],
     // 修改提示输入框
      ruleFormUpdate: {
        name: "",
        id:0
      },
      // 添加渲染数据
      list2:[]
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    // 获取列表数据
    async getList(num = 1, size = 5) {
      let { data } = await this.$http.get(
        `categories?type=${this.type}&pagenum=${num}&pagesize=${size}`
      );
      // console.log(data)
      this.list = data.data.result;
      this.total = data.data.total;
      this.pagenum = data.data.pagenum;
      this.pagesize = data.data.pagesize;
      console.log(this.list, this.total);
    },
    // 分页部分
    // num必须要有默认值不然就报错
    handleSizeChange(size, num = this.pagenum) {
      this.getList(num, size);
    },
    // 这里是size必须有默认值，不然不行
    handleCurrentChange(num, size = this.pagesize) {
      this.getList(num, size);
    },
    // 添加提交预校验
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitAdd()
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 确认提交
    async submitAdd(){
      console.log(111)
        let data = await this.$http.post("categories",{
          cat_pid :this.ruleForm.value.length+1,
          cat_name:this.ruleForm.name,
          cat_level:this.ruleForm.value.length
        })
        console.log(data)
        console.log(this.ruleForm.value.length)
        this.dialogFormVisible = false;
        this.getList()
    }
    ,
    // 重置触发
    resetForm(formName) {
      this.dialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    // 添加商品
    async add() {
      this.getList2()
      this.dialogFormVisible = true;
    },
  // 删除事件
  del(id){
    // 删除
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          console.log(id);
          this.$http.delete("categories/" + id).then((res) => {
            console.log(res);
            // 刷新必须要放在里面
             this.getList()
          });
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
  },
   // 修改
    update(i) {
      this.dialogFormVisibleUpdate = true;
      this.ruleFormUpdate.name = i.cat_name;
      this.ruleFormUpdate.id = i.cat_id;
      console.log(this.ruleFormUpdate);
    },
    // 确定修改
    async updated() {
      let { data } = await this.$http.put("categories/" + this.ruleFormUpdate.id, {
        cat_name: this.ruleFormUpdate.name,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "更新成功！",
          type: "success",
        });
      }
      console.log(data.data);
      this.dialogFormVisibleUpdate = false;
      this.getList()
    },
  // 获取添加数据
   async getList2(num = 1, size = this.total) {
      let { data } = await this.$http.get(
        `categories?type=${this.type}&pagenum=${num}&pagesize=${size}`
      );
       data.data.result.map((i)=>{
        if(i.children){
          i.children.map((is)=>{
            delete is.children
          })
        }
      })
      console.log(data.data.result)
      this.list2 =data.data.result
      console.log(this.list2)
    },
  },
  mounted() {
    this.getList();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss">
.el-card {
  margin-top: 15px;
}
/* 表格样式 */

.el-table {
  margin-top: 15px;
}
.el-table__body tr .el-button,
.el-table__body td .el-button {
  width: 70px;
  padding: 7px;
  /* font-size: 12px !important; */
}
/* 排序的样式 */
.el-table td .hmw {
  width: 38px;
  height: 20px;
  color: white;
  border-radius: 5px;
  font-size: 12px;
  text-align: center;
}
.el-cascader{
  width: 100%;
}
</style>
