<template>
  <div class="users">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主题部分 -->
    <el-card class="box-card">
      <!-- 搜索部分 -->
      <!-- 栅栏布局 -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
            v-model="query"
            placeholder="请输入内容"
            class="input-with-select"
          >
            <el-button
              @click="getUser()"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button
            type="primary"
            size="mini"
            @click="dialogFormVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>
      <!-- 表格部分 -->
      <el-table border :data="users" stripe style="width: 100%">
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="username" label="姓名" width="180">
        </el-table-column>
        <el-table-column prop="email" label="邮箱" width="180">
        </el-table-column>
        <el-table-column prop="mobile" label="电话" width="180">
        </el-table-column>
        <el-table-column prop="role_name" label="角色" width="180">
        </el-table-column>
        <el-table-column prop="mg_state" label="状态" width="180">
          <template slot-scope="scope">
            <!-- 开关 -->
            <el-switch
              @change="change(scope.row.id, scope.row.mg_state)"
              v-model="scope.row.mg_state"
              active-color="#409EFF"
              inactive-color="#DCDFE6"
            >
            </el-switch>
          </template>
        </el-table-column>
        <!-- 操作按钮 -->
        <el-table-column label="操作" prop="id" width="180">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="update(scope.row)"
              
            ></el-button>
            <!-- 删除 -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="delUser(scope.row.id)"
            ></el-button>
            <!-- 修改角色 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="分配权限"
              placement="top-start"
            >
              <el-button
                size="mini"
                type="warning"
                icon="el-icon-setting"
                @click="role(scope.row.id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          height="50"
          :current-page="pagenum"
          :page-sizes="[2, 4, 6, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!-- 添加的弹框 -->
    <el-dialog title="添加用户" :visible.sync="dialogFormVisible">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="ruleForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <!-- 这里的参数必须要是和ref一样的!!! -->
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改的弹框 -->
    <el-dialog title="编辑用户" :visible.sync="dialogFormVisibleUpdate">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名">
          <el-input v-model="ruleFormUpdate.name" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="ruleFormUpdate.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="phone">
          <el-input v-model="ruleFormUpdate.phone"></el-input>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleUpdate = false">取 消</el-button>
        <el-button type="primary" @click="updateForm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    // 电话号码验证
    let isPhone = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("手机号不能为空"));
      } else if (/^1[3456789]\d{9}$/.test(value)) {
        return callback();
      } else {
        return callback("手机号码有误，请重填");
      }
    };
    return {
      // 搜索部分
      search: "",
      users: [], //数据
      pagesize: 2,
      pagenum: 1,
      query: "", //查询关键字
      total: 0,
      //   添加弹框里的数据
      dialogFormVisible: false,
      dialogFormVisibleUpdate: false,
      formLabelWidth: "100px",
      // 添加提示输入框
      ruleForm: {
        username: "",
        password: "",
        email: "",
        phone: "",
      },
      // 修改提示输入框
      ruleFormUpdate: {
        name: "",
        email: "",
        phone: "",
        id: "",
      },
      // 规则有这个才有小红点
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"],
          },
        ],
        phone: [
          // { required: true, message: "请输入电话", trigger: "blur" },
          {required: true, validator: isPhone, trigger: "blur" },
        ],
      },
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    // 先获取一下用户列表数据
    // 必须要有形参，不然报错
    async getUser(num = 1, size = 2) {
      let { data } = await this.$http.get(
        `users?query=${this.query}&pagenum=${num}&pagesize=${size}`
      );
      this.users = data.data.users;
      this.pagesize = size;
      this.pagenum = num;
      this.total = data.data.total;
      console.log(data.data);
    },
    // 添加
    async addUser() {
      // await this.submitForm('ruleForm')
      let add = await this.$http.post("users", {
        username: this.ruleForm.username,
        password: this.ruleForm.password,
        email: this.ruleForm.email,
        mobile: this.ruleForm.phone,
      });
      console.log(add);
      if (add.data.meta.status == 201) {
        this.$message({
          message: "添加成功！啦啦啦！",
          type: "success",
        });
      // 清空一下
        this.ruleForm = { username: "", password: "", email: "", phone: "" };
        this.dialogFormVisible = false;
      } else {
        this.$message({
          message: "添加失败",
          type: "warning",
        });
      }
      // // 相当于刷新
      this.getUser(1, 2);
    },
    // 添加提交预校验
   submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.addUser()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      // 修改提交预校验
   updateForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.updated()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      // 重置触发
      resetForm(formName) {
        this.dialogFormVisible = false
        this.$refs[formName].resetFields();
      },
    // 修改
    update(i) {
      this.dialogFormVisibleUpdate = true;
      this.ruleFormUpdate.name = i.username;
      this.ruleFormUpdate.email = i.email;
      this.ruleFormUpdate.phone = i.mobile;
      this.ruleFormUpdate.id = i.id;
      console.log(this.ruleFormUpdate);
    },
    // 确定修改
    async updated() {
      let { data } = await this.$http.put("users/" + this.ruleFormUpdate.id, {
        id: this.ruleFormUpdate.id,
        email: this.ruleFormUpdate.email,
        mobile: this.ruleFormUpdate.phone,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "更新成功！",
          type: "success",
        });
      }
      console.log(data.data.id);
      this.dialogFormVisibleUpdate = false;
      this.getUser()
    },
    // 删除
    delUser(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          console.log(111);
          this.$http.delete("users/" + id).then((res) => {
            console.log(res);
            this.getUser()
          });
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
        
    },
    // 修改角色
    role(i) {
      alert("我还是没写分配权限！");
    },
    // 分页部分
    // num必须要有默认值不然就报错
    handleSizeChange(size, num = this.pagenum) {
      this.getUser(num, size);
    },
    // 这里是size必须有默认值，不然不行
    handleCurrentChange(num, size = this.pagesize) {
      this.getUser(num, size);
    },
    // 修改状态
    async change(id, flag) {
      console.log(id, flag);
      let { data } = await this.$http.put(`users/${id}/state/${flag}`);
      console.log(data);
      if (data.meta.status == 200) {
        this.$message({
          message: "修改状态成功！",
          type: "success",
        });
      }
    },
  },
  mounted() {
    this.getUser(1, 2);
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style lang="scss" scoped>
/* 卡片部分 */
.box-card {
  margin-top: 15px;
}
.el-col {
  display: flex;
  height: 50px;
  .el-button {
    height: 40px;
  }
}
// 表格部分(网上搜索部分)
.el-table__header tr,
.el-table__header th {
  padding: 0;
  height: 30px;
  line-height: 30px;
}
.el-table__body tr,
.el-table__body td {
  padding: 4px 0;
  // height: 30px;
  // line-height: 30px;
  .el-button {
    width: 46px;
    padding: 10px;
    height: 30px;
    .i {
      height: 80%;
      top: 0;
    }
  }
}
.el-table {
  border-radius: 5px;
}

// 分页部分
.el-pagination {
  height: 20px;
  margin-top: 15px;
  .el-pager {
    height: 100%;
  }
}
</style>
